<template>
	<div class="personal-content-body">
		<div class="table-content">
			<div class="table-group">
				<div class="table-group-content">
					<div class="table-group-tab">
						<div class="table-group-tab-item" :class="{'current':type==0}" @click="changeTab(0)">管理员</div>
						<div class="table-group-tab-item" :class="{'current':type==1}" @click="changeTab(1)">转让管理员</div>
						<div class="clear"></div>
						<div class="line"></div>
					</div>
					<div class="transfer_box" v-if="type==0">
						<div class="transfer_group">
							<div class="transfer_group_left">超级管理员</div>
							<div class="transfer_group_right">
								<div class="transfer_userbox" v-for="(item,index) in superadminList">
									<div class="transfer_imgbox">
										<img class="transfer_img_userimg" :src="item.url" @error="getImgurl" alt="">
										<img class="transfer_img_span" src="../../images/transferManager_span.png" alt=""></img>
									</div>
									<div class="transfer_textbox">{{item.name}}</div>
								</div>
							</div>
						</div>
						<div class="transfer_group">
							<div class="transfer_group_left">子管理员</div>
							<div class="transfer_group_right">
								<div class="transfer_userbox" v-for="(item,index) in childAdminList">
									<div class="transfer_imgbox">
										<img class="transfer_img_userimg" :src="item.url" @error="getImgurl" alt="">
										<img class="transfer_img_delete" v-if="is_admin==2" src="../../images/transferManager_delete.png" alt="" @click="deleteManager(item.uid)"></img>
									</div>
									<div class="transfer_textbox">{{item.name}}</div>
								</div>
								<div class="transfer_addbox" v-if="is_admin==2" @click="addManager">
								</div>
							</div>
						</div>
					</div>
					<div class="transfer_box" v-if="type==1">
						<div class="transfer_group">
							<div class="transfer_group_left">管理员</div>
							<div class="transfer_group_right">
								<div class="transfer_userbox">
									<div class="transfer_imgbox">
										<img class="transfer_img_userimg" :src="userInfo.url" @error="getImgurl" alt="">
										<img class="transfer_img_span" v-if="is_admin==2" src="../../images/transferManager_span.png" alt=""></img>
									</div>
									<div class="transfer_textbox">{{userInfo.username}}</div>
								</div>
							</div>
						</div>
						<div class="transfer_group">
							<div class="transfer_group_left">转移给</div>
							<div class="transfer_group_right">
								<div class="transfer_addbox" @click="transferManager" v-if="is_admin==1||is_admin==2">
								</div>
							</div>
						</div>
					</div>
					<!--<add-people-modal :visibleDialog="adminVisibleDialog" name="admin" :selectedId="adminIdList" @changeshow="changeModalShow" @setData="getModaldata" title="选择默认抄送人"></add-people-modal>-->
					
					<add-people-modal1 :visibleDialog="transferAdminVisibleDialog" name="transferAdmin" :selectedId="adminIdList1" @changeshow="changeModalShow" @setData="getModaldata1" title="选择转移人"></add-people-modal1>
					
					
					<!--  设置权限弹窗开始-->
					<el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
						<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
						<div slot="footer" class="dialog_footer clear">
							<button class="dialog_btn dialog_btn_cancel" @click="setPowerCancel">取消</button>
							<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
						</div>
					</el-dialog>
					<!--设置权限弹窗结束-->
					
					
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import setPower from '../../components/setPower.vue'
	
	import { operateAdmin,setPowerSubmitApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn, getStore } from '../../config/mUtils'
	import api from '../../config/url.js'
	import addPeopleModal from '../applyManage/addPeopleModal1.vue' //左部模块
	import addPeopleModal1 from '../applyManage/addPeopleModal3.vue' //右部模块
	export default {
		data() {
			return {
				userInfo: '',
				is_admin: 0,
				type: 0, //0=>资讯列表1=>发布历史  
				adminVisibleDialog: false,
				transferAdminVisibleDialog: false,
				managerList: [],
				managerIdList: [],
				superadminList: [],
				childAdminList: [],
				adminList: [],
				adminIdList: [],
				adminIdList1: [],
				
				// 设置权限模块
				isSetPowerDialogShow: false,
				havePowerUserIds: [],
				subComponentPostIds: [],
				ids: '', //设置权限时选择的用户ids
			}
		},
		components: {
			addPeopleModal,
			addPeopleModal1,
			
			setPower
		},
		created() {
			let _that = this;
			this.userInfo = JSON.parse(getStore('htcx_userInfo'));
			this.is_admin = this.userInfo.is_admin;
			this.initData();
		},
		computed: {},
		mounted() {},
		methods: {
			// 选择管理员模块开始
			// 获取设置权限组件返回来的ids
			getSetPowerIds(idNodes) {
				console.log(idNodes);
				this.subComponentPostIds = [];
				for(let i = 0, max = idNodes.length; i < max; i++) {
					if(this.subComponentPostIds.indexOf(idNodes[i]) == -1) {
						this.subComponentPostIds.push(idNodes[i])
					}
				}
				var postIds = this.decodeSelectedUserId(this.subComponentPostIds);
				this.ids = postIds.join(',');
			},
			//给加标识的员工id进行解码还原为id数组
			decodeSelectedUserId(data) {
				var user_ids = '';
				var selectedEmployeeId = [];
				var splitArr = [];
				for(var i = 0, max = data.length; i < max; i++) {
					splitArr = data[i].id.split('-');
					if(selectedEmployeeId.indexOf(splitArr[0]) == -1) {
						selectedEmployeeId.push(splitArr[0]);
					}
				}
				return selectedEmployeeId
			},
			setPowerSubmit() {
				operateAdmin({
					flag: 2,
					idStr: this.ids
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '添加成功')
				this.havePowerUserIds = [],
						
						this.isSetPowerDialogShow = false;
						this.initData();
					} else {
						dialogConfirm('fail', res.data.message)
					}
				})
			},
			setPowerCancel(){
				this.havePowerUserIds = [],
				this.isSetPowerDialogShow = false;
			},
			addManager() {
				this.isSetPowerDialogShow = true;
			},
			// 选择管理员模块开始
			
			
			initData() {
				this.managerList = [];
				this.managerIdList = [];
				this.superadminList = [];
				this.childAdminList = [];
				this.adminList = [];
				this.adminIdList = [];
				this.adminIdList1 = [];
				let _that = this;
				if(this.is_admin == 0) {
					dialogConfirm('info', '您没有分配管理员权限')
					setTimeout(function() {
						_that.$router.push({
							name: 'administrativeManageHomepage'
						})
					}, 1000)
				} else {
					operateAdmin({
						flag: 1
					}).then(res => {
						if(res.data.msgNo == 1) {
							let ml = res.data.payload;
							ml.forEach(item => {
								this.adminList.push(item);
								this.adminIdList.push(item.uid);
								if(item.is_admin == 2) {
									this.superadminList.push(item)
								} else if(item.is_admin == 1) {
									this.childAdminList.push(item);
								}
							})
							if(this.is_admin == 1) {
								this.adminIdList1 = JSON.parse(JSON.stringify(this.adminIdList));
							} else if(this.is_admin == 2) {
								this.adminIdList1.push(this.userInfo.uid)
							}
						}
					})
				}
			},
			changeTab(id) {
				this.type = id;
			},
			
			transferManager() {
				this.transferAdminVisibleDialog = true;
			},
			deleteManager(id) {
				dialogConfirmHasbtn('确认删除吗？').then(res => {
					operateAdmin({
						flag: 4,
						id: id
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm('success', '删除成功')
							this.initData();
						} else {
							dialogConfirm('fail', res.data.message)
						}
					})
				}).catch(res => {})
			},
			///////////////////////////弹窗数据传递
			//修改弹窗显示
			changeModalShow(type, name) {
				this[name + 'VisibleDialog'] = type
			},
			getModaldata(name, list) {
				if(list.length != 0) {
					operateAdmin({
						flag: 2,
						idStr: list.toString()
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm('success', '添加成功')
							this.initData();
						} else {
							dialogConfirm('fail', res.data.message)
						}
					})
				} else {}
			},
			getModaldata1(list, liststr) {
				let user = list[0]
				if(list.length != 0) {
					dialogConfirmHasbtn(user.name + '将成为管理员，你将立刻失去管理员身份（转让管理员后将会自动退出），确定要转让吗？。').then(res => {
						operateAdmin({
							flag: 3,
							id: liststr[0]
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm('success', '转移成功')
								//this.initData();
								window.localStorage.clear();
								window.sessionStorage.clear();
								api.resetServer();
								this.$router.push({
									path: '/login'
								})
							} else {
								dialogConfirm('fail', res.data.message)
							}
						})
					}).catch(() => {})
				} else {

				}

			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			},
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/modal.scss';
	.table-content{
		padding: 20px 12px;
	}
	.transfer_box {
		padding: 0px 18px;
		.transfer_title {
			font-size: 18px;
			color: #35383A;
			font-weight: bold;
			padding-left: 9px;
		}
		.transfer_group {
			display: flex;
			margin-top: 10px;
		}
		.transfer_group_left {
			font-size: 14px;
			color: #808080;
			width: 120px;
			height: 60px;
			line-height: 60px;
			text-align: left;
			margin-right: 40px;
			flex-shrink: 0;
		}
		.transfer_group_right {
			display: flex;
			flex-wrap: wrap;
		}
		.transfer_userbox {
			display: flex;
			flex-direction: column;
			padding: 8px 15px;
			align-items: center;
		}
		.transfer_imgbox {
			width: 40px;
			height: 40px;
			position: relative;
		}
		.transfer_img_userimg {
			width: 100%;
			height: 100%;
			border-radius: 50%;
		}
		.transfer_img_span {
			width: 10px;
			height: 10px;
			position: absolute;
			right: 0px;
			bottom: 0px;
		}
		.transfer_img_delete {
			width: 10px;
			height: 10px;
			position: absolute;
			right: 0px;
			top: 0px;
			cursor: pointer;
		}
		.transfer_textbox {
			font-size: 14px;
			color: #35383A;
			margin-top: 5px;
		}
		.transfer_addbox {
			cursor: pointer;
			display: inline-block;
			width: 40px;
			height: 40px;
			border: 1px solid #999;
			border-radius: 50%;
			position: relative;
			margin: 8px 15px;
			&::before {
				content: '';
				display: inline-block;
				position: absolute;
				width: 27px;
				height: 2px;
				top: 18px;
				left: 6px;
				background-color: #999;
			}
			&::after {
				content: '';
				display: inline-block;
				position: absolute;
				width: 2px;
				height: 27px;
				top: 6px;
				left: 18px;
				background-color: #999;
			}
			&:hover {
				border-color: #3C98E8;
				&::before {
					background: #3C98E8;
				}
				&::after {
					background: #3C98E8;
				}
			}
		}
	}
	/*设置权限弹出框*/
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	/*设置权限弹出框结束*/
</style>


